@import "./styles/variable.scss";


.login-set {
  &-link {
    color: $primaryColor;
    transition: 0.3s;
    &:hover {
      color: #41bc9d;
    }
  }
  &-user {
    height: 75px;
    margin-bottom: 15px;
  }
  &-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px !important;
    position: absolute;
    // right: 0px;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 99;
    min-width: 350px;
    input {
      border: 1px solid #ccc;
      border-radius: 5px;
      padding: 7px 12px;
      font-size: 14px !important;
      font-weight: 300;
      margin-bottom: 10px;
    }
    // button {
    //   border: 1px solid $primaryColor;
    //   background: $primaryColor;
    //   font-size: 1em;
    //   font-weight: 300;
    //   padding: 7px 12px;
    //   transition: 0.3s;
    //   &:hover {
    //     background-color: $primaryColor;
    //   }
    // }
  }
}

.profile-dp {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #ccc;
}

.login-set-card {
  margin-top: 30px !important;
}